body {
  margin: 0;
  padding: 0;
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 17px;
  padding-right: 15px;
  background-color: #1b2d37; }

@media (min-width: 768px) {
  .page-header {
    padding: 25px;
    padding-top: 20px; } }

@media (min-width: 1024px) {
  .page-header {
    padding-top: 33px;
    max-width: 1400px;
    margin: 0 auto; } }

.page-header__call-button {
  display: none; }

@media (min-width: 1024px) {
  .page-header__call-button {
    display: block;
    padding-top: 14px;
    padding-bottom: 13px;
    width: 180px;
    color: #fff;
    font-family: Roboto Condensed, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border: 1px solid #FFFFFF;
    border-radius: 5px;
    background-color: transparent;
    cursor: pointer; } }

.page-header__logo-tablet {
  display: none; }

@media (min-width: 768px) {
  .page-header__logo-mobile {
    display: none; }
  .page-header__logo-tablet {
    display: flex; } }

.page-header__logo-text {
  margin-left: 16px; }
  .page-header__logo-text p:first-child {
    margin: 0;
    font-family: Roboto Condensed;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 29px;
    color: #FFFFFF; }
  .page-header__logo-text p:last-child {
    margin: 0;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #788891; }

.page-header__icon-wrapper {
  display: flex;
  align-items: center; }

@media (min-width: 1024px) {
  .page-header__icon-wrapper {
    display: none; } }

.page-header__icons-desktop {
  display: none; }

@media (min-width: 1024px) {
  .page-header__icons-desktop {
    display: flex;
    font-size: 16px;
    line-height: 18px;
    color: #FFFFFF; } }

.desktop-mail {
  display: flex;
  align-items: center; }
  .desktop-mail svg {
    margin-right: 20px; }
  .desktop-mail p {
    margin: 0; }

.desktop-phone {
  margin-left: 88px;
  margin-right: 38px;
  display: flex;
  align-items: center; }
  .desktop-phone svg {
    margin-right: 16px; }
  .desktop-phone p {
    margin: 0; }

.page-header__trigger {
  position: relative;
  display: block;
  margin-bottom: 10px;
  width: 37px;
  height: 1px;
  background-color: #fff; }
  .page-header__trigger:after {
    position: absolute;
    bottom: -10px;
    content: "";
    width: 37px;
    height: 1px;
    background-color: #fff; }

.page-header__icon-mail {
  margin-left: 27px; }

@media (min-width: 768px) {
  .page-header__icon-mail {
    margin-left: 37px; } }

.page-header__icon-phone {
  margin-left: 23px; }

@media (min-width: 768px) {
  .page-header__icon-phone {
    margin-left: 33px; } }

.page-footer {
  position: fixed;
  bottom: 0;
  width: 100%; }

.page-footer__logo {
  display: flex;
  margin: 0 auto;
  margin-top: 46px;
  margin-bottom: 26px;
  width: 216px;
  align-items: center; }

.page-footer__logo-text {
  margin-left: 16px; }
  .page-footer__logo-text p:first-child {
    margin: 0;
    font-family: Roboto Condensed;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 29px;
    color: #203C42; }
  .page-footer__logo-text p:last-child {
    margin: 0;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #788891; }

.page-footer__created {
  margin: 0;
  margin-bottom: 31px;
  font-family: PT Mono;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #1B2D37;
  opacity: 0.5;
  text-align: center; }

.page-footer__social-list {
  margin: 0 auto;
  margin-bottom: 54px;
  padding: 0;
  list-style: none;
  display: flex;
  width: 112px;
  justify-content: space-between; }

.page-footer__accordion input {
  display: none; }

.page-footer__accordion p {
  margin: 0;
  padding-left: 15px;
  padding-bottom: 23px;
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  color: #1B2D37;
  opacity: 0.5;
  width: 235px; }

.page-footer__accordion label {
  padding-left: 15px;
  padding-top: 29px;
  padding-bottom: 28px;
  font-family: Roboto Condensed, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 16px;
  display: flex;
  align-items: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1B2D37; }

.page-footer__accordion-list {
  margin: 0;
  padding: 0;
  list-style: none; }
  .page-footer__accordion-list .page-footer__accordion-item:first-child {
    border-top: 1px solid #cdcdcd; }

.page-footer__accordion-item {
  border-bottom: 1px solid #cdcdcd; }
