@font-face {
  font-family: 'Roboto';
	font-style: normal;
	font-weight: normal;
	src:
		url('../fonts/Roboto-Regular.woff2') format('woff2'), 
		url('../fonts/Roboto-Regular.woff') format('woff'),
		url('../fonts/Roboto-Regular.ttf') format('ttf');
}

